
import tools from '../../lib/tools.js';
import tabBar from '../../components/TabBar.vue';
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name:'doctorNews',
components:{Tab, TabItem,Toast,tabBar},
directives: {TransferDom},
data(){
return {
toastMsg:'',
showToast:false,
id:'',// userid
tabItemFlag:true,
errorImg:'this.src="' + require('../../assets/header333x.png') + '"' ,
doctorList:[],
systemList:[]
}
},
mounted(){
let id = (localStorage.getItem('md') || tools.getCookie('user_id'));//md === userid
if((!id) || id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}// 此时可以跳转到登陆，等登陆后在跳转回来
this.id = id;
this.initDoctor();
this.initSystem();
},
methods:{
initDoctor(){
let vm = this;
this.$http.post('/message/countUnReadMessageList',{currentPage:1,showCount:100,userId:this.id}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.doctorList = data.items;
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
initSystem(){
let vm = this;
this.$http.axios({method:'POST',url:'/message/getPageMessages',data:{pageSize:1,pageNumber:100,messageTo:this.id,type:2}}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.systemList = data.items;
}else{
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
lookMsg(item){
tools.next('/m/messageIndex.html?id='+item.messageFrom);
},
updateState(item,index){
let vm = this;
this.$http.post('/message/readOverMessage',{userId:this.id,messageId:item.id,type:2}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.systemList[index].status = 1;
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
}
}
}



