import Vue from 'vue';
import messages from './Messages.vue';
import axios from '../../lib/axios.config';

Vue.prototype.$http = axios;
Vue.config.productionTip = false;

const FastClick = require('fastclick')
FastClick.attach(document.body);

new Vue({
  el: '#app',
  template: '<messages/>',
  components: { messages }
})


